body {
  margin: 0;
  font-family: 'Montserrat Alternates','Golos Text',source-code-pro, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* make border box */
 

}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

code {
  font-family: 'Montserrat Alternates','Golos Text',source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
